.wrap {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  transition: height 250ms ease-out 0s;
  flex-direction: column;
  max-height: 276vh;
  display: flex;
}

.item {
  width: 32.33333333%;
  margin: 5px;
  height: fit-content;
  //display: inline-flex;
}

.wrapp {

  transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1);
}

.item img {
  width: 100%;
}

